const Evento = (props) => {
    
    const {name, url, description} = props.evento

    let titulo = name.text;
    if(titulo.length > 150){
        titulo = titulo.substr(0,150) + '...'
    }

    let desc = description.text;
    if(desc){
        desc = desc.substr(0,250) + '...'
    }

    return(
        <a href={url} target="_black" className="list-group-item active text-light mb-1">
            <h3 className="mb-3">{name.text}</h3>
            <p className="mb-1">{description.text}</p>

        </a>
    )
}

export default Evento;